<template>
  <div id="app">
    <v-app dark>
      <v-toolbar app>
        <v-toolbar-title>Sango2武将资料</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-text-field v-model="general" label="武将名" class="pr-2"></v-text-field>
          <v-layout wrap style="width:300px">
            <v-autocomplete
              v-model="selectSkills"
              class="pr-2"
              :no-data-text="''"
              :items="allSkills"
              :filter="skillsCustomFilter"
              item-value="id"
              item-text="name"
              label="武将技"
              multiple
            ></v-autocomplete>
          </v-layout>
          <v-layout wrap style="width:300px">
            <v-autocomplete
              v-model="wiseSkills"
              class="pr-4"
              :no-data-text="''"
              :items="allWiseSkills"
              :filter="skillsCustomFilter"
              item-value="id"
              item-text="name"
              label="军师技"
              multiple
            ></v-autocomplete>
          </v-layout>
          <v-btn @click="dosearch" icon :disabled="updateresult">
            <v-icon>search</v-icon>
          </v-btn>
          <v-btn @click="doclear" icon :disabled="updateresult">
            <v-icon>refresh</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-content fluid>
        <v-container>
          <v-card v-for="g in faces" :key="g.face" style="float:left;" class="ma-1">
            <a>
              <img
                :id="'img-' + g.face"
                :src="'static/face/FACE' + g.face + '_1-1.png'"
                width="100"
                height="120"
                class="ma-3"
                @mouseover="onImgMouseover('img-' + g.face)"
                @mouseout="onImgMouseout('img-' + g.face)"
                @click="onImgClick(g.id)"
              >
            </a>
            <v-card-title class="mt-0 pt-0">
              <v-spacer></v-spacer>
              {{g.name}}
              <v-spacer></v-spacer>
            </v-card-title>
          </v-card>
        </v-container>
        <v-dialog v-model="infoDialog" width="500">
          <v-card>
            <v-card-title class="headline lighten-2 pb-0" primary-title>
              {{ infoGeneral.NAME }}
              <br>
              <sub class="caption">{{ tableItems[0][0]+tableItems[0][1] + '  ' + tableItems[0][2]+tableItems[0][3]
                  + '  ' + tableItems[0][4]+tableItems[0][5] + '  ' + tableItems[0][6]+tableItems[0][7] }}</sub>
            </v-card-title>
            <v-card-text class="pt-0">
              <v-data-table
              :headers="skillTableHeader"
              :items="skillTableItems"
              :hide-actions="true"
              :no-data-text="''"
            >
              <template slot="items" slot-scope="props">
                  <td class="text-xs-right pr-1">{{ props.item.skillLevel }}</td>
                  <td class="text-xs-left pl-1 pr-1">{{ props.item.skill }}</td>
                  <td class="text-xs-right pl-1 pr-1">{{ props.item.wiseSkillLevel }}</td>
                  <td class="text-xs-left pl-1">{{ props.item.wiseSkill }}</td>
                </template>
              </v-data-table>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" flat icon @click="infoDialog = false">
                <v-icon>close</v-icon>
              </v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-content>
    </v-app>
  </div>
</template>

<script>
import times from "./times";
import skills from "./skills";
import wiseSkills from "./wiseSkills";

var tempfaces = [];

export default {
  name: "App",
  components: {},
  data() {
    return {
      faces: [],
      updateresult: false,
      general: "",
      selectSkills: [],
      allSkills: skills,
      wiseSkills: [],
      allWiseSkills: wiseSkills,
      infoDialog: false,
      infoGeneral: {},
      tableItems: [
        ['武力:',null,'智力:',null,'体力:',null,'技力:',null]
      ],
      skillTableHeader: [
        { text: "等级", sortable: false, class: "text-xs-right pr-1" },
        { text: "武将技", sortable: false, class: "text-xs-left pl-1 pr-1" },
        { text: "等级", sortable: false, class: "text-xs-right pl-1 pr-1" },
        { text: "军师技", sortable: false, class: "text-xs-left pl-1" }
      ],
      skillTableItems:[
        
      ]
    };
  },
  created() {
    for (let i = 0; i < this.allWiseSkills.length; i++) {
      if (this.allWiseSkills[i].level) {
        var s = "";
        if (this.allWiseSkills[i].level == 1) {
          s = "(初)";
        } else if (this.allWiseSkills[i].level == 2) {
          s = "(中)";
        } else if (this.allWiseSkills[i].level == 3) {
          s = "(高)";
        } else {
        }
        this.allWiseSkills[i].name += s;
      }
    }
  },
  methods: {
    updateFaces() {
      var len = this.faces.length;
      if (len < tempfaces.length) {
        this.faces.push(tempfaces[len]);
        setTimeout(this.updateFaces, 0);
      } else {
        this.updateresult = false;
      }
    },
    dosearch() {
      tempfaces = [];
      for (let i = 1; i < times[0].GENERALS.length; i++) {
        var faceName = null;
        if (i < 10) {
          faceName = "00" + i;
        } else if (i < 100) {
          faceName = "0" + i;
        } else {
          faceName = "" + i;
        }
        var check = true;
        if (this.general) {
          if (!(times[0].GENERALS[i].NAME.indexOf(this.general) > -1)) {
            check = false;
          }
        }
        if (check && this.selectSkills.length) {
          var skillChecks = true;
          var selectSkillsChecks = [];
          for (let k = 0; k < this.selectSkills.length; k++) {
            selectSkillsChecks[k] = false;
            for (let j = 0; j < times[0].GENERALS[i].SKILL.length; j++) {
              if (this.selectSkills[k] == times[0].GENERALS[i].SKILL[j][1]) {
                selectSkillsChecks[k] = true;
                break;
              }
            }
          }
          for (let m = 0; m < selectSkillsChecks.length; m++) {
            if (!selectSkillsChecks[m]) {
              skillChecks = false;
              break;
            }
          }
          if (!skillChecks) {
            check = false;
          }
        }
        if (check && this.wiseSkills.length) {
          var skillChecks = true;
          var selectSkillsChecks = [];
          for (let k = 0; k < this.wiseSkills.length; k++) {
            selectSkillsChecks[k] = false;
            for (let j = 0; j < times[0].GENERALS[i].WISESKILL.length; j++) {
              if (this.wiseSkills[k] == times[0].GENERALS[i].WISESKILL[j][1]) {
                selectSkillsChecks[k] = true;
                break;
              }
            }
          }
          for (let m = 0; m < selectSkillsChecks.length; m++) {
            if (!selectSkillsChecks[m]) {
              skillChecks = false;
              break;
            }
          }
          if (!skillChecks) {
            check = false;
          }
        }
        if (check) {
          tempfaces.push({
            id: times[0].GENERALS[i].SEQUENCE,
            face: faceName,
            name: times[0].GENERALS[i].NAME
          });
        }
      }
      this.faces = [];
      this.updateresult = true;
      this.updateFaces();
    },
    doclear() {
      this.faces = [];
      (this.selectSkills = []), (this.wiseSkills = []), (this.general = null);
    },
    onImgMouseover(id) {
      document.getElementById(id).style.borderColor = "yellow";
    },
    onImgMouseout(id) {
      document.getElementById(id).style.borderColor = "black";
    },
    onImgClick(id) {
      for (let i = 1; i < times[0].GENERALS.length; i++) {
        if (times[0].GENERALS[i].SEQUENCE == id) {
          this.infoGeneral = times[0].GENERALS[i];
          break;
        }
      }
      this.tableItems[0][1] = this.infoGeneral.STR;
      this.tableItems[0][3] = this.infoGeneral.INT;
      this.tableItems[0][5] = this.infoGeneral.BaseHP;
      this.tableItems[0][7] = this.infoGeneral.BaseMP;

      this.skillTableItems = [];
      for (let i=0; i<this.infoGeneral.SKILL.length; i++) {
        var item = {};
        item.skill = this.allSkills[this.infoGeneral.SKILL[i][1]-1].name;
        item.skillLevel = this.infoGeneral.SKILL[i][0];
        this.skillTableItems.push(item);
      }
      for (let i=0; i<this.infoGeneral.WISESKILL.length; i++) {
        var item = {};
        item.wiseSkill = this.allWiseSkills[this.infoGeneral.WISESKILL[i][1]-1].name;
        item.wiseSkillLevel = this.infoGeneral.WISESKILL[i][0];
        if (this.skillTableItems[i]) {
          this.skillTableItems[i].wiseSkill = item.wiseSkill;
          this.skillTableItems[i].wiseSkillLevel = item.wiseSkillLevel;
        } else {
          this.skillTableItems.push(item);
        }
      }
      
      this.infoDialog = true;
    },
    skillsCustomFilter(item, queryText, itemText) {
      var value1 = itemText.toLowerCase();
      var value2 = queryText.toLowerCase();
      return value1.indexOf(value2) > -1;
    }
  },
  mounted() {
    this.updateFaces();
  }
};
</script>

<style>
img {
  border: 1px solid black;
}
</style>



